import React, {PureComponent} from 'react';
import '../../style/App.css';
import {connect} from 'react-redux';
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import Subscriber from "./Subscriber";
import LoAd from "../../conponent/load";
import MenuHeader from "../../Header/MenuHeader";
import Cookies from "js-cookie";
import {FollowPeople, GetUser} from "./SubscriberReducer";
import {Loading} from "../Login/LoginReducer";
import {getAllPeople, getPeople} from "../../user-selector/PeopleSelector";

class SubscriberCont extends PureComponent {

    state = {
        people: this.props.people
        , allPeople: this.props.allPeople

        , currentPage: 1
        , pageSize: 4
    };

    componentDidMount() {
        let page = {
            currentPage: this.state.currentPage
            , page: this.state.pageSize
        }
        this.props.GetUser(page)
        Cookies.set('History', 'subscribers')
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        debugger
        if (prevState.people !== this.props.people)
            this.setState({
                people: this.props.people,
                allPeople: this.props.allPeople
            })
    }

    Click = (currentPage) => {
        let page = {
            currentPage: currentPage
            , page: this.state.pageSize
        }
        this.props.GetUser(page);
        this.setState({
            currentPage: currentPage
        })
    };
    Subscribe = (p, id) => {
        let friend = {
            friend: p
            , IdFriend: id
        }
        this.props.FollowPeople(friend)
    };

    render() {
        return (
            <div>
                <div>
                    <div>
                        <MenuHeader/>
                    </div>

                    <div className={'SUBSCRIBERS'}>
                        {this.props.state.loading ? <LoAd/> : null}
                        <Subscriber users={this.state.people || []}
                                    currentPage={this.state.currentPage}
                                    pageSize={this.state.pageSize}
                                    allPeople={this.state.allPeople}

                                    clic={this.Click}
                                    Subscribe={this.Subscribe}

                                    load={this.props.Loading}/>
                    </div>
                </div>
            </div>
        )
    }
}

let mapState = (state) => {
    return {
        people: getPeople(state)
        , allPeople: getAllPeople(state)
    }
};
export default compose(
    connect(mapState, {GetUser, Loading, FollowPeople})
    , withAuthRedirect
)(SubscriberCont)